import { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import usePlayersData from '../hooks/usePlayersData'
import { updateHasToPlay } from '../redux/features/playersSlice'
import { hideMenu, updateMenuHeight } from '../redux/features/gameSlice'
import { useRef } from 'react'

export default function RulesMenu() {
  const { activePlayers } = usePlayersData()
  const dispatch = useDispatch()

  const windowRef = useRef()

  const closeMenu = () => {
    activePlayers.forEach((item) => {
      item.isActive &&
        dispatch(updateHasToPlay({ player: item, hasToPlay: true }))
    })
    dispatch(hideMenu('rules'))
  }

  useEffect(() => {
    dispatch(
      updateMenuHeight({
        menu: 'rules',
        height: windowRef.current.clientHeight,
      })
    )
  }, [])

  return (
    <div className="rulesMenu">
      <div onClick={closeMenu} className="rulesMenu__overlay"></div>
      <div className="rulesMenu__window" ref={windowRef}>
        <h1 className="rulesMenu__header">Rgles</h1>
        <h2 className="rulesMenu__title">I  Droulement de la partie</h2>
        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            En dbut de partie tous les joueurs reoivent cinq ds alatoire.
            Seuls eux doivent connatre la valeur de leurs ds.
          </p>
          <p className="rulesMenu__paragraph">
            Le premier joueur doit ensuite faire une enchre. Il doit annoncer
            une valeur ded et un nombre de ds, principalement en fonction de
            son tirage. Thoriquement, il doit se rapprocher au mieux de la
            ralit, mais ilpeut aussi bluffer.
          </p>
          <p className="rulesMenu__paragraph">
            Cest ensuite au joueur suivant de se prononcer. Il a trois choix :
          </p>
        </section>
        <ul className="rulesMenu__list">
          <li>
            Surenchrir : Il peut estimer que laffirmation du premier joueur
            est plausible et il doit alors annoncer une surenchre,
          </li>
          <li>
            Dudo : Il peut estimer que laffirmation du premier joueur est
            fausse et il doit annoncer  dudo  (je doute),
          </li>
          <li>
            Calza : Il peut estimer que laffirmation du premier joueur est
            parfaitement juste et il doit alors annoncer  calza  (cal).
          </li>
        </ul>
        <h3 className="rulesMenu__subtitle">{'A) Surenchre'}</h3>
        <p className="rulesMenu__body">
          Pour surenchrir, le joueur doit choisir une seule des deux options
          suivantes :
        </p>
        <ul className="rulesMenu__list">
          <li>Soit augmenter la valeur des ds,</li>
          <li>Soit augmenter le nombre de ds.</li>
        </ul>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            Une fois quil a fait ce choix, il doit annoncer sa nouvelle
            proposition et cest au joueur situ  sa gauche de parler. Ce
            dernier peut dire  dudo  ou surenchrir et ainsi de suite.
          </p>
          <p className="rulesMenu__paragraph">
            Exemple : Le joueur prcdent a annonc  il y a au moins 5 ds de
            valeur 4 autour de cette table . On peut annoncer  il y a eu moins
            7 ds de valeur 4 autour de cette table  (dans ce cas, on a
            augment le nombre de ds). Ou,  il y a au moins 5 ds de valeur 5
            autour de cette table  (dans ce cas, cest la valeur des ds qui a
            t augmente).
          </p>
        </section>

        <h3 className="rulesMenu__subtitle">{'B) Dudo (je doute)'}</h3>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            Tous les joueurs rvlent leurs ds. On voit alors si la proposition
            du joueur prcdent est exacte ou fausse.
          </p>
          <p className="rulesMenu__paragraph">
            Si la proposition du joueur prcdent est exacte (cest--dire quil
            y a un nombre de ds de la valeur choisie suprieur ou gal  la
            proposition du joueur prcdent), le joueur qui vient dannoncer 
            dudo  a perdu la manche : il perd un de ses ds.
          </p>
          <p className="rulesMenu__paragraph">
            Si la proposition du joueur prcdent est fausse (cest--dire quil
            y a un nombre de ds de la valeur choisie infrieur  la proposition
            du joueur prcdent), cest ce dernier qui a perdu la manche et il
            perd un de ses ds.
          </p>
          <p className="rulesMenu__paragraph">
            Un joueur qui perd son dernier d est limin de la partie.
          </p>
        </section>

        <h3 className="rulesMenu__subtitle">{'C) Calza (cal)'}</h3>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            Tous les joueurs rvlent leurs ds. On voit alors si la proposition
            du joueur prcdent est parfaitement juste ou non.
          </p>
          <p className="rulesMenu__paragraph">
            Si la proposition du joueur prcdent parfaitement juste
            (cest--dire quil y a un nombre de ds de la valeur choisie
            parfaitement gal  la proposition du joueur prcdent), le joueur
            qui vient dannoncer  calza  a gagner la manche : il gagne un d
            dans la limite des 5 ds de dpart. Le joueur prcdent conserve son
            nombre de ds.
          </p>
          <p className="rulesMenu__paragraph">
            Dans le cas o la proposition du joueur prcdent nest pas
            parfaitement juste alors le joueur qui vient dannoncer  calza  a
            perdu la manche : il perd un de ses ds.
          </p>
          <p className="rulesMenu__paragraph">
            Un joueur qui perd son dernier d est limin de la partie.
          </p>
        </section>

        <h2 className="rulesMenu__title">II  Manche suivante</h2>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            Lorsquune manche se termine suite  un  dudo  ou  calza  et
            quun joueur a perdu (ou gagn) un d, on recommence une nouvelle
            manche mlangeant  nouveau les ds alatoirement. Le joueur qui
            vient de perdre (ou gagner) un d est le nouveau premier joueur.
            Sil vient dtre limin de la partie, cest le joueur suivant qui
            prend ce rle.
          </p>
        </section>

        <h2 className="rulesMenu__title">III  Cas particuliers</h2>

        <h3 className="rulesMenu__subtitle">{'A) Les ds  Paco '}</h3>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            Les ds de valeur 1 sont spciaux, ce sont des jokers. Tout dabord,
            ils servent de joker lorsque les ds sont rvls suite  un  dudo
            . Ils ont toujours la mme valeur que celle de la dernire
            proposition exprime.
          </p>
          <p className="rulesMenu__paragraph">
            Exemple : on rvle les ds suite  un  dudo  annonc par un
            joueur. La proposition prcdente est  il y a au moins 9 ds de
            valeur 3 autour de cette table . Pour savoir si cette affirmation
            est exacte ou fausse, on ajoute les paco aux ds qui indiquent 3.
          </p>
        </section>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            On peut proposer des paco. Normalement, on ne peut quaugmenter la
            valeur des ds. Mais les paco sont spciaux et on peut parfaitement
            annoncer une proposition qui concerne les paco : Toutefois, il faut
            au moins annoncer un nombre de paco gal  la moiti de la
            proposition prcdente. On arrondit toujours  lentier suprieur.
          </p>
          <p className="rulesMenu__paragraph">
            Exemple : Le joueur prcdent a annonc  il y a eu moins 7 ds de
            valeur 4 autour de cette table . Le joueur suivant peut dire  il y
            a au moins 4 ds paco autour de cette table .
          </p>
        </section>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            Notez que cest le seul moment du jeu o on peut rduire la valeur
            du d propos.
          </p>
        </section>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            Une fois quun joueur a annonc un certain nombre de paco, le joueur
            suivant peut soit augmenter le nombre de paco proposs, soit changer
            la valeur des ds. Mais dans ce cas prcis, il doit au moins
            proposer un nombre de ds gal  la proposition prcdente plus un.
          </p>
        </section>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            Exemple : Si un joueur a annonc 4 paco, le joueur suivant, sil
            souhaite augmenter la valeur du d, doit au moins annoncer 9 ds (le
            double de la proposition prcdente plus 1).
          </p>
        </section>

        <h3 className="rulesMenu__subtitle">{'B) Etre  Palifico '}</h3>

        <section className="rulesMenu__body">
          <p className="rulesMenu__paragraph">
            Quand un joueur na plus quun d dans son gobelet, il est 
            palifico  pendant une manche. Tant quun joueur est  palifico 
            les paco ne sont pas considrs comme des jokers et aucun joueur ne
            peut modifier la valeur du d propos par le premier joueur.
          </p>
        </section>

        <div onClick={closeMenu} className="rulesMenu__okBtn">
          ok
        </div>
      </div>
    </div>
  )
}
